.container{
    background-image: linear-gradient(150deg, #BCA0DC 0%, #804FB3 100%);
    height: 100vh;
}

.container .card{
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-60%);
    min-width: 20rem;
    padding: 0.8rem
}

.container h3{
    margin: auto;
    color: var(--dark-purple);
    font-family: Roboto-Bold;
    font-size: 1.8rem;
}
.container p{
    font-family: Roboto-Light;
    padding: 0px 0.5rem;
}

.container p b{
    color: var(--dark-purple); 
}
.card__footer{
    display: flex;
    align-items: center;
    justify-content: center;
}
.start-btn{
    background-color: var(--dark-purple);
    color: #fff;
    font-family: Roboto-Bold;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 0.5rem 1rem;
    text-decoration: none;
    text-align: center;
    font-size: 0.9rem;
    display: inline-block;
}


.start-btn:hover{
    background-color: #3c0044f5;
}